import { parse, DocumentNode } from "graphql";

const sitePluginType = `
  type SitePlugin implements Node @dontInfer {
    resolve: String
    name: String
    version: String
    nodeAPIs: [String]
    pluginFilepath: String
    pluginOptions: JSON
    packageJson: JSON
  }
`;

const allSdlTypes = [sitePluginType];

export const builtInTypeDefinitions = (): Array<DocumentNode> =>
  allSdlTypes.map((type) => parse(type));

export const builtInScalarTypeNames = [
  `Boolean`,
  `Date`,
  `Float`,
  `ID`,
  `Int`,
  `JSON`,
  `String`,
  `GatsbyImageData`,
];

export const internalTypeNames = [
  ...builtInScalarTypeNames,
  `Buffer`,
  `Internal`,
  `InternalInput`,
  `Node`,
  `NodeInput`,
  `Query`,
];
